import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { CreatePackageComponent } from './components/create-package/create-package.component';
import { DeliveryHistoryComponent } from './components/delivery-history/delivery-history.component';
import { DeliverySetupComponent } from './components/delivery-setup/delivery-setup.component';

const routes: Routes = [
	{ path: 'create-package', component: CreatePackageComponent},
	{ path: 'delivery-history', component: DeliveryHistoryComponent},
  { path: 'delivery-manage', component: DeliveryHistoryComponent},
  { path: 'delivery-pending', component: DeliveryHistoryComponent},
	{ path: 'delivery-setup', component: DeliverySetupComponent},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class ParcelRoutingModule { }
